// Import Nunito font
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800;900&display=swap");

body,
html {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
  background-color: #f6fafc;
  font-family: "Nunito", sans-serif;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a:link {
  color: #727cf5;
}

p {
  color: #59626a;
}

div#root {
  height: 100%;
}

div#root > div:first-child {
  height: 100%;
}

label {
  display: block;
  text-transform: uppercase;
  font-size: 0.6875rem;
  font-weight: 700;
  color: rgb(127, 143, 164);
  letter-spacing: 0.15em;
  margin-bottom: 8px;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.rs-grid-container-fluid {
  width: 100%;
}

.rs-btn {
  padding: 8px 16px;
  border-radius: 3px;
}

.rs-btn-primary {
  background-color: #727cf5;
}

.rs-btn-primary:not(.rs-btn-disabled):hover {
  background-color: #565db5;
}

.rs-btn-link,
.rs-btn-link:hover {
  color: #727cf5;
}

.rs-btn-ghost {
  color: #1d2830;
  border: 1px solid #1d2830;
}

.rs-btn-lg {
  font-weight: 600;
}

.rs-input {
  background-color: rgb(255, 255, 255);
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  border-radius: 3px;
  border: solid 1.5px #d6dbe0;
  transition: all 0.2s ease 0s;
  outline: transparent dashed 1px;
}

.rs-picker-select a {
  border-radius: 3px;
  border: solid 1.5px #d6dbe0;
  line-height: 26px;
}

.rs-sidebar-wrapper.fixed {
  position: fixed;
  background: #313a46;
  border-right: 1px solid #d9d9d9;
  padding-top: 36px;
  max-width: 260px;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  transition: width 1s;
}
@media (min-width: 1220px) {
  .rs-sidebar-wrapper.fixed {
    position: static;
  }
}

.rs-nav ul li a {
  color: #ffffff !important;
  opacity: 0.5;
}

.rs-nav ul li.rs-nav-item.rs-nav-item-active a {
  opacity: 1;
}

.rs-sidenav .rs-nav.rs-nav-vertical ul > .rs-dropdown:not(:first-child),
.rs-sidenav .rs-nav.rs-nav-vertical ul > .rs-nav-item:not(:first-child) {
  height: 50px;
}

.rs-panel {
  background: #ffffff;
}

.rs-nav-subtle.rs-nav-horizontal .rs-nav-item > .rs-nav-item-content::before {
  background-color: rgb(121, 76, 255);
}

.rs-checkbox label {
  text-transform: none;
  font-size: 13px;
}

.inline-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.mt-xs {
  margin-top: 5px;
}

.mt-sm {
  margin-top: 10px;
}

.mt-md {
  margin-top: 15px;
}

.mt-lg {
  margin-top: 20px;
}

.mt-xl {
  margin-top: 50px;
}

.hide {
  display: none;
}

.page-context {
  // margin-left: 260px;
  padding: 86px 15px;
  background: inherit;
  transition: margin-left 1s;
  position: relative;
}

.page-context.stretched {
  margin-left: 70px;
}

.page-context-title {
  font-size: 24px;
  margin-bottom: 10px;
}
